body {
  background: rgb(204,204,204); 
}
#page {
  background: white;
  display: block;
  margin: 0 auto;
  margin-bottom: 0.5cm;
  box-shadow: 0 0 0.5cm rgba(0,0,0,0.5);
}
#page[size="A4"] {  
  width: 21cm;
  height: 29.7cm; 
  margin: 0;
}
#page[size="A4"][layout="landscape"] {
  width: 29.7cm;
  height: 21cm;  

}
#page[size="A3"] {
  width: 29.7cm;
  height: 42cm;
}
#page[size="A3"][layout="landscape"] {
  width: 42cm;
  height: 29.7cm;  
}
#page[size="A5"] {
  width: 14.8cm;
  height: 21cm;
}
#page[size="A5"][layout="landscape"] {
  width: 21cm;
  height: 14.8cm;  
}
@media print {
  #body, page {
    margin: 0;
    box-shadow: 0;
  }
}
body {  
  background-color: white;
  font-family: Arial, Helvetica, sans-serif;
}

.container {
  margin: 2rem auto;
  max-width: 600px;
  background-color: #fbfbfb;
  border-radius: 20px;
}

.suggestionSection {
  height: 400px;
}

#question {
  margin-top: 4rem;
  color: #33383e;
  font-family: 'Permanent Marker';
  font-size: 3rem;
}

.options {
  margin: 1rem 0 4rem .25rem
}


#activityCount {
  padding-left: .25rem;
}

h2, #activityList {
  color: black;
}

#first-row {
  padding: 5% 5% 0 5%;
}

#second-row {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

#primary-btn {
  color: #fff;
  background-color: #007bff;
  border-color: #007bff;
  text-align: center;
  vertical-align: middle;
  padding: .375rem .75rem;
  margin: 0 1rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: .25rem;
}

#primary-btn:hover {
  color:#007bff;
  background-color: #fff;
  cursor: pointer;
}

#success-btn {
  color: #fff;
  background-color: #28a745;
  border-color: #28a745;
  text-align: center;
  vertical-align: middle;
  padding: .375rem .75rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: .25rem;
}

#success-btn:hover {
  color:#28a745;
  background-color: #fff;
  cursor: pointer;
}

#danger-btn {
  color: #fff;
  background-color: #dc3545;
  border-color: #dc3545;
  text-align: center;
  vertical-align: middle;
  padding: .375rem .75rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: .25rem;
}

#danger-btn:hover {
  color:#dc3545;
  background-color: #fff;
  cursor: pointer;
}

#fourth-row {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding-bottom: 2em;
}

@media only screen and (max-width: 600px) {
  .container {
    max-width: 350px;
  }

  h1 {
    margin-top: 2.5rem;
    font-size: 2rem;
  }
}
